.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}
.cat-box {
    border-radius: 70%;
    width: 300px;
    height: 300px;
    overflow: hidden;
}

.cat-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.loading {
    margin-top: 5rem;
    margin-bottom: 5rem;
    width: 100px;
    height: 100px;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
